import React from "react";
import {metadata} from './metadata.js';
import {Context} from "./context.js"

export default class SmokerLengend extends React.Component{
  static contextType = Context;
  
  render(){
      var index = this.context.state.dataindex
      console.log(index)
      var legend=[];
      for (var i =1; i<metadata[1]['break'].length;i++) {
          legend.push(
          <div key={i} style={{display: 'flex',alignItems: 'center'}}>
              
              <span style ={{backgroundColor:metadata[index]['color'][i], width: '15px', height: '15px', display: 'inline-block'}}>
              </span>
                  {metadata[index]['break'][i-1]}-{metadata[index]['break'][i]-1}
              
              </div>); 
          
      }
      return legend.reverse();
  
}
}